<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <CommonMissStationHeader></CommonMissStationHeader>
      </div>

      <el-form
        class=""
        style="padding: 0 60px 0 20px; width: 700px; margin: 20px 0 60px 0"
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="none"
      >
        <el-form-item prop="finished_product_code" label="产品编码">
          <el-input
            ref="firstInput"
            v-model="form.finished_product_code"
            @input="inputChange()"
            clearable
          ></el-input>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 表格 -->
    <el-card class="box-card mt20">
      <div slot="header" class="clearfix">
        <span>过站信息</span>
      </div>
      <div class="">
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="true"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>
    <!-- 不良信息弹框 -->

    <el-dialog title="不良原因" width="600px" :visible.sync="dialogFlagBadInfo">
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        :model="dialogBadInfoForm"
        label-width="100px"
      >
        <el-form-item label="不良现象：" prop="unqualified_result_name">
          <span>{{ dialogBadInfoForm.unqualified_result_name }}</span>
        </el-form-item>

        <el-form-item label="不良原因：" prop="content">
          <span>{{ dialogBadInfoForm.content }}</span>
        </el-form-item>
        <el-form-item label="处理方式：" prop="process_mode">
          <span>{{ dialogBadInfoForm.process_mode }}</span>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 维修弹框 -->
    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <div v-if="dialogTitle == '产品维修'">
          <el-form-item label="产品编码">
            <span>{{ dialogForm.finished_product_code }}</span>
          </el-form-item>

          <el-row class="elRow">
            <el-col :span="4">工站编码</el-col>
            <el-col :span="4">物料名称</el-col>
            <el-col :span="12">物料编码</el-col>
            <el-col :span="4">操作</el-col>
          </el-row>
          <el-divider></el-divider>
          <div
            v-for="(item, index) in dialogForm.response_datas"
            :key="index + ' onlyItem'"
          >
            <el-row class="elRow">
              <el-col :span="4">{{ item.work_id }}</el-col>
              <el-col :span="4">{{ item.matter_name }}</el-col>
              <el-col :span="12">
                <!-- <span :v-once="true">{{ item.matter_id }}</span> -->
                <span v-if="vOnceFlag" v-once>{{ item.matter_id }}</span>
                <span v-else>{{ item.matter_id }}</span>
              </el-col>
              <el-col :span="4">
                <span
                  style="color: #409eff; cursor: pointer"
                  @click="editMatter(item, index)"
                >
                  修改</span
                >
              </el-col>
            </el-row>

            <el-row class="elRow">
              <el-col :span="4"> </el-col>
              <el-col :span="4"> </el-col>
              <el-col :span="12">
                <el-form-item
                  v-show="item.show"
                  :prop="'response_datas.' + index + '.matter_id'"
                  label-width="40px"
                  label=""
                  :rules="[
                    {
                      required: true,
                      message: '物料编码不能为空',
                      trigger: 'blur',
                    },
                    {
                      pattern: item.rule,
                      message: '物料编码需要包含' + item.rule,
                      trigger: 'blur',
                    },
                    {
                      min: item.code_length,
                      max: item.code_length,
                      message:
                        '物料编码长度为 ' + item.code_length + '个字节。',
                      trigger: 'blur',
                    },
                  ]"
                >
                  <el-input
                    :ref="'matterInput' + index"
                    v-model="item.matter_id"
                    clearable
                    @input="inputMatter(index)"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="4"></el-col>
            </el-row>
            <el-divider></el-divider>
          </div>
        </div>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
import CommonMissStationHeader from "@/components/CommonMissStationHeader";
import ELTable from "@/components/formTablePage/ElTable";

export default {
  name: "MissStationM",
  components: {
    CommonMissStationHeader,
    ELTable,
  },
  created() {
    window.addEventListener("keydown", this.handleKeyDown, true); //开启监听键盘按下事件
  },
  data() {
    return {
      dialogFlagBadInfo: false,
      dialogFlag: false,
      vOnceFlag: false,
      timeout: "",
      unqualifiedData: [],
      dialogSaveFlag: true,
      Package_Qty: "",
      Shipping_SN_length: "",
      Product_length: "",
      finished_product_codeFlag: false,

      // 产品过站弹出层
      form: {
        status: "",
        product_plan_code: "",
        finished_product_code: "",
      },
      formBase: {
        status: "",
        product_plan_code: "",
        finished_product_code: "",
      },
      formRules: {
        finished_product_code: [
          { required: true, message: "请输入产品编码", trigger: "blur" },
        ],
      },

      // 筛选
      listQuery: {
        status: "入站",
        product_plan_code: this.ProductionPlanCode,
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        status: "入站",
        product_plan_code: this.ProductionPlanCode,
        page: 1,
        page_size: 10,
        total: 0,
      },

      dialogBadInfoForm: {
        unqualified_result_name: "",
        content: "",
        process_mode: "",
      },
      dialogBadInfoFormBase: {
        unqualified_result_name: "",
        content: "",
        process_mode: "",
      },

      dialogForm: {
        modify_in_workstation_code: "",
        finished_product_code: "",
        product_plan_code: "",
        process_method: "",
        return_work_id: "",
        status: "",
        response_datas: [],
      },
      dialogFormBase: {
        modify_in_workstation_code: "",
        finished_product_code: "",
        product_plan_code: "",
        process_method: "",
        return_work_id: "",
        status: "",
        response_datas: [],
      },
      ispagination: true, // 分页是否显示
      // 表格列

      tableCol: [
        {
          label: "生产计划",
          field: "product_plan_name",
          align: "center",
        },
        // {
        //   label: "产品名称",
        //   field: "product_name",
        //   align: "center",
        // },
        {
          label: "产品编码",
          field: "finished_product_code",
          align: "center",
        },

        // {
        //   label: "工站名称",
        //   field: "work_name",
        //   align: "center",
        // },
        {
          label: "操作人员",
          width: "200px",
          field: "operate_user_name",
          align: "center",
        },

        {
          label: "处理方式",
          field: "process_method",
          align: "center",
        },
        // {
        //   label: "状态",
        //   field: "status",
        //   align: "center",
        // },

        {
          label: "来源工站",
          field: "enter_work_name",
          align: "center",
        },
        {
          label: "入站时间",
          field: "enter_time",
          align: "center",
        },

        {
          label: "出站时间",
          field: "out_time",
          align: "center",
        },

        {
          label: "操作",
          colType: "solt",
          align: "center",
          isShow: (row) => {
            return row.status == "入站" ? true : false;
          },
          option: [
            {
              label: "不良原因",
              event: this.colBadInfoBtn,
            },
            {
              label: "维修",
              event: this.colEditBtn,
            },
            {
              label: "拆解",
              event: this.colResetBtn,
            },
            {
              label: "出站",
              event: this.colReturnBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },

  computed: {
    ...mapState([
      "ProductionPlanCode",
      "BomMatterCodeNameList",
      "WorkCodeNameList",
      "ProductPlanCodeNameList",
      "PersonCodeNameList",
    ]),
  },
  methods: {
    ...mapActions([
      "getLogisticBomMatterCodeName",
      "getLogisticWorkCodeName",
      "getLogisticProductPlanDeal",
      "getLogisticProductPlanCodeName",
      "getLogisticPersonCodeName",
    ]),
    operate_type_change() {
      if (this.dialogForm.operate_type == "产品拆解") {
        this.dialogForm.response_datas = [];
      } else {
        this.getLogisticFinishedCodeGetMatterCodeID(this.form);
      }
    },

    // 打印参数列表 查询
    getLogisticOperationSystem() {
      baseManagement
        .getLogisticOperationSystem({
          page: 1,
          page_size: 10,
        })
        .then((res) => {
          console.log("打印参数列表 查询", res);
          if (res.code == 0) {
            this.Package_Qty = res.data.data[0].Package_Qty;
            this.Shipping_SN_length = res.data.data[0].Shipping_SN_length;
            this.Product_length = res.data.data[0].Product_length;

            console.log("this.Package_Qty ", this.Package_Qty);
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    // 产品码返回一个不良原因编码
    getLogisticProductCodeResponseResult(params) {
      baseManagement
        .getLogisticProductCodeResponseResult(params)
        .then((unqualified_res) => {
          console.log("产品码返回一个不良原因编码", unqualified_res);
          if (unqualified_res.code == 0) {
            if (unqualified_res.data.length == 0) {
              this.$message.warning("没有不良原因！");
              return false;
            }
            this.dialogFlagBadInfo = true;

            //  工站和不良原因表的接口
            baseManagement
              .getLogisticWorkUnqualifiedResult({
                unqualified_result_code: unqualified_res.data[0].description,
                page: 1,
                page_size: 10,
              })
              .then((res) => {
                console.log("工站和不良原因表的接口", res);
                if (res.code == 0) {
                  this.unqualifiedData = res.data.data;
                  console.log("this.unqualifiedData", this.unqualifiedData);
                  this.dialogBadInfoForm.unqualified_result_name =
                    res.data.data[0].unqualified_result_name;
                  this.dialogBadInfoForm.content = res.data.data[0].content;
                  this.dialogBadInfoForm.process_mode =
                    res.data.data[0].process_mode;
                } else {
                  this.$message({
                    message: res.message,
                    type: "error",
                  });
                }
              });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    inputChange(e) {
      if (this.timeout) {
        clearTimeout(this.timeout);
      }
      this.timeout = setTimeout(() => {
        // this.dialogForm.response_datas=[]
        if (this.form.finished_product_code.length != this.Product_length) {
          this.$message.warning(
            "产品编码长度为" + this.Product_length + "个字节。"
          );
          // this.finished_product_codeFlag = false;
          return false;
        }

        this.form.finished_product_code = this.form.finished_product_code;
        this.form.product_plan_code = this.ProductionPlanCode;
        this.form.status = "入站";

        let params = JSON.parse(JSON.stringify(this.form));

        this.postLogisticModifyInWorkStation(params);

        // this.finished_product_codeFlag = true;
      }, 500);
    },
    editMatter(item, index) {
      item.matter_id = "";
      item.show = !item.show;
      this.$nextTick(() => {
        this.$refs["matterInput" + index][0].focus();
      });
    },
    inputMatter(index) {
      console.log(this.dialogForm.response_datas[index]);
      this.timeout = setTimeout(() => {
        this.dialogSaveFlag = true;

        if (this.dialogForm.response_datas[index].matter_id == "") {
          this.$message.warning("物料编码不能为空");
          this.dialogSaveFlag = false;
          return false;
        }
        if (
          this.dialogForm.response_datas[index].matter_id.indexOf(
            this.dialogForm.response_datas[index].rule
          ) < 0
        ) {
          this.$message.warning(
            "物料编码需要包含" + this.dialogForm.response_datas[index].rule
          );
          this.dialogSaveFlag = false;
          return false;
        }
        if (
          this.dialogForm.response_datas[index].matter_id.length !=
          this.dialogForm.response_datas[index].code_length
        ) {
          this.$message.warning(
            "物料编码长度为" +
              this.dialogForm.response_datas[index].code_length +
              "个字节。"
          );
          this.dialogSaveFlag = false;
          return false;
        }
      }, 500);
    },
    // 通过产品编码查询物料 查询
    getLogisticFinishedCodeGetMatterCodeID(params) {
      baseManagement
        .getLogisticFinishedCodeGetMatterCodeID(params)
        .then((res) => {
          console.log("通过产品编码查询物料 查询", res);
          if (res.code == 0) {
            let resData = res.data;
            resData.forEach((resDataItem, index) => {
              this.BomMatterCodeNameList.forEach((BomMatterCodeNameItem) => {
                // console.log("resDataItem resDataItem resDataItem", resDataItem);
                // console.log(
                //   "BomMatterCodeNameItem BomMatterCodeNameItem -=",
                //   BomMatterCodeNameItem
                // );
                if (
                  resDataItem.matter_code == BomMatterCodeNameItem.matter_code
                ) {
                  resDataItem.matter_name = BomMatterCodeNameItem.matter_name;
                  resDataItem.rule = BomMatterCodeNameItem.rule;
                  resDataItem.code_length = BomMatterCodeNameItem.code_length;
                }
                resDataItem.show = BomMatterCodeNameItem.false;
              });
            });
            this.dialogForm.response_datas = res.data;
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },
    // 重 置
    getDeleteFinishedCodeGetMatterCodeID(params) {
      baseManagement
        .getDeleteFinishedCodeGetMatterCodeID(params)
        .then((res) => {
          console.log("重 置 删除", res);
          if (res.code == 0) {
            this.getLogisticModifyInWorkStation(this.listQuery);
            this.$message({
              message: res.message,
              type: "success",
            });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },

    // 通过产品编码查询物料
    getLogisticPutFinishedCodeGetMatterCodeID(params) {
      baseManagement
        .getLogisticPutFinishedCodeGetMatterCodeID(params)
        .then((res) => {
          console.log("产品过站 修改", res);
          if (res.code == 0) {
            // this.dialogFlag = false;
            this.vOnceFlag = false;
            this.getLogisticModifyInWorkStation(this.listQuery);
            this.$message({
              message: res.message,
              type: "success",
            });
          } else {
            this.$message({
              message: res.message,
              type: "error",
            });
          }
        });
    },

    handleKeyDown(e) {
      let key = null;
      if (window.event === undefined) {
        key = e.keyCode;
      } else {
        key = window.event.keyCode;
      }
      if (key === 13) {
        this.dialogSave();
      }
    },

    // 过站查询 查询
    getLogisticModifyInWorkStation(params) {
      baseManagement.getLogisticModifyInWorkStation(params).then((res) => {
        console.log("过站查询 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.ProductPlanCodeNameList.forEach((CodeNameListItem) => {
              if (
                tableDataItem.product_plan_code ==
                CodeNameListItem.product_plan_code
              ) {
                tableDataItem.product_plan_name =
                  CodeNameListItem.product_plan_name;
              }
            });
            this.WorkCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.enter_work_code == CodeNameListItem.work_code) {
                tableDataItem.enter_work_name = CodeNameListItem.work_name;
              }
            });

            this.PersonCodeNameList.forEach((CodeNameListItem) => {
              if (tableDataItem.operate_user == CodeNameListItem.user_code) {
                tableDataItem.operate_user_name = CodeNameListItem.user_name;
              }
            });
          });

          this.tableData = tableData;
          this.vOnceFlag = true;

          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    // 过站 新增
    postLogisticModifyInWorkStation(params) {
      baseManagement.postLogisticModifyInWorkStation(params).then((res) => {
        console.log("过站 新增", res);
        if (res.code == 0) {
          this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
          this.form.finished_product_code = "";
          this.getLogisticModifyInWorkStation(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticModifyInWorkStation(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticModifyInWorkStation(this.listQuery);
    },
    // 不良原因
    colBadInfoBtn(rowData) {
      console.log(rowData);
      // this.dialogBadInfoForm
      //  产品码返回一个不良原因编码
      this.dialogBadInfoForm = JSON.parse(
        JSON.stringify(this.dialogBadInfoFormBase)
      );

      this.getLogisticProductCodeResponseResult({
        finished_product_code: rowData.finished_product_code,
      });
    },
    // 维修

    colEditBtn(rowData) {
      console.log(rowData);

      if (!this.ProductionPlanCode) {
        this.$message.warning("没有进行中的生产计划！");
        return false;
      }
      this.dialogFlag = true;
      this.dialogTitle = "产品维修";
      this.dialogForm.process_method = "产品维修";
      this.dialogForm.status = "入站";
      this.dialogForm.finished_product_code = rowData.finished_product_code;
      this.dialogForm.return_work_id = rowData.enter_work_code;
      this.dialogForm.modify_in_workstation_code =
        rowData.modify_in_workstation_code;
      this.getLogisticFinishedCodeGetMatterCodeID(this.dialogForm);
    },
    // 拆解
    colResetBtn(rowData) {
      console.log(rowData);

      if (!this.ProductionPlanCode) {
        this.$message.warning("没有进行中的生产计划！");
        return false;
      }
      this.$confirm(
        "确定执行产品拆解操作吗？此操作将解绑所有与该产品绑定的物料。",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).then(() => {
        this.dialogForm.finished_product_code = rowData.finished_product_code;
        this.dialogForm.process_method = "产品拆解";
        this.dialogForm.status = "入站";
        this.dialogForm.modify_in_workstation_code =
          rowData.modify_in_workstation_code;
        this.getDeleteFinishedCodeGetMatterCodeID(this.dialogForm);
      });
    },
    // 出站
    colReturnBtn(rowData) {
      console.log(rowData);

      if (!this.ProductionPlanCode) {
        this.$message.warning("没有进行中的生产计划！");
        return false;
      }
      this.dialogForm.process_method = "产品出站";
      this.dialogForm.finished_product_code = rowData.finished_product_code;
      this.dialogForm.status = "出站";
      this.dialogForm.enter_work_code = rowData.enter_work_code;
      this.dialogForm.return_work_id = rowData.enter_work_code;
      this.dialogForm.modify_in_workstation_code =
        rowData.modify_in_workstation_code;
      this.getLogisticFinishedCodeGetMatterCodeID(this.dialogForm);
      this.$confirm("确定执行出站操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        console.log("this.form.response_datas", this.response_datas);
        let params = JSON.parse(JSON.stringify(this.dialogForm));
        params.product_plan_code = this.ProductionPlanCode;
        params.response_datas.forEach((item) => {
          item.show = "";
        });

        params.response_datas = JSON.stringify(params.response_datas);
        console.log("params params params", params);
        this.getLogisticPutFinishedCodeGetMatterCodeID(params);
      });
    },

    // 确定
    dialogSave() {
      if (!this.dialogSaveFlag) {
        this.$message.warning("请输入正确的物料编码！");
        return false;
      }
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
        }
      });
      console.log("this.form", this.form);

      let params = JSON.parse(JSON.stringify(this.dialogForm));
      params.product_plan_code = this.ProductionPlanCode;
      params.response_datas.forEach((item) => {
        item.show = "";
      });

      params.response_datas = JSON.stringify(params.response_datas);
      console.log("params params params", params);
      this.getLogisticPutFinishedCodeGetMatterCodeID(params);
    },
  },
  mounted() {
    // 打印参数列表 查询
    this.getLogisticOperationSystem();
    // 工站内码和工站名称接口： 查询
    this.getLogisticWorkCodeName();
    // 进行中的生产计划接口：
    this.getLogisticProductPlanDeal();
    // 产品　物料code和名称接口 查询
    this.getLogisticBomMatterCodeName();
    // 生产计划内码和名称接口 查询
    this.getLogisticProductPlanCodeName();
    // 人员内码和人员名称 查询
    this.getLogisticPersonCodeName();
    // 过站查询 查询
    this.listQuery.product_plan_code = this.ProductionPlanCode;

    this.getLogisticModifyInWorkStation(this.listQuery);

    this.$nextTick(() => {
      this.$refs.firstInput.focus();
    });
  },
};
</script>

