<template>
  <div>
    <span class="boxDivSpanFontSize"> 生产计划 : </span>
    <span class="">{{ ProductionPlanName }}</span>

    <span class="ml20 boxDivSpanFontSize"> 工站编码 : </span>
    <span class="">{{ CurrentUserInfo.work_id }}</span>

    <span class="ml20 boxDivSpanFontSize">工站名称 : </span>
    <span>{{ workStationName }}</span>
    <span class="ml20 boxDivSpanFontSize">工站类型 : </span>
    <span>{{ CurrentUserInfo.work_type }}</span>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";

import { mapState, mapActions } from "vuex";
export default {
  computed: {
    ...mapState(["WorkCodeNameList", "ProductionPlanCode", "CurrentUserInfo"]),
  },
  created() {},
  data() {
    return {
      workStationName: "",
      ProductionPlanName: "",
    };
  },
  methods: {
    ...mapActions(["getLogisticWorkCodeName", "getLogisticProductPlanDeal"]),

    async getMissStationHeaderWorkName() {
      // 工站内码和工站名称接口： 查询
      await this.getLogisticWorkCodeName();

      this.WorkCodeNameList.forEach((CodeNameListItem) => {
        if (this.CurrentUserInfo.work_id == CodeNameListItem.work_id) {
          this.workStationName = CodeNameListItem.work_name;
        }
      });
    },
  },
  mounted() {
    baseManagement
      .getLogisticProductPlanDeal({
        prodect_code: "",
        plan_name: "",
        plan_status: "进行中",
        page: 1,
        page_size: 10,
        total: 0,
      })
      .then((res) => {
        if (res.code == 0) {
          console.log(111, res);
          console.log(111, res.data.data[0]);
          this.ProductionPlanName = res.data.data[0].plan_name;
        }
      });
    this.getMissStationHeaderWorkName()
    // 进行中的生产计划接口：
    // this.getLogisticProductPlanDeal();

    // this.$nextTick(() => {
    //   this.WorkCodeNameList.forEach((CodeNameListItem) => {
    //     if (this.CurrentUserInfo.work_id == CodeNameListItem.work_id) {
    //       this.workStationName = CodeNameListItem.work_name;
    //     }
    //   });

    // });
  },
};
</script>

<style lang="scss" scoped>
.boxDivSpanFontSize {
  font-weight: 600;
}
</style>